import React, { Component } from 'react';

class ContactForm extends Component {
    render() {
        return(
            <form action="" method="post" className="contactForm">
                <div className="row">
                    <div className="col-md-6">
                        <div className="form-group">
                            <input type="text" name="contactName" size="40" className="form-control" aria-required="true" aria-invalid="false" placeholder="Your Name..." />
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="form-group">
                            <input type="email" name="contactEmail" size="40" className="form-control" aria-required="true" aria-invalid="false" placeholder="Please Enter Your Email..." />
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <input type="text" name="contactSubject" size="40" className="form-control" aria-required="true" aria-invalid="false" placeholder="Please Enter a Subject..." />
                </div>
                <div className="form-group">
                    <textarea name="contactMessage" cols="40" rows="10" className="form-control" aria-required="true" aria-invalid="false" placeholder="Your Message..."></textarea>
                </div>
                <p>
                    <button type="submit" className="btn btn-light w-25" value="SEND">SEND</button>
                    <span className="ajax-loader"></span>
                </p>
            </form>
        );
    }
}

export default ContactForm;