import React, { Component } from 'react';
import Experiences from './Experiences';
import ContactForm from './ContactForm';

class SectionItem extends Component {
  render() {
      switch(this.props.section.title) {
        case 'About':
            return(
                <div className="collapse" id={'collapseContent'+this.props.section.id} data-parent="#collapseContent"> 
                    <div className="row">
                        <div className="container">
                            <div className="col-12">
                                <div id="contentContainer">
                                    <div id="title">A little something about me…</div>
                                    <p>Starting out as a Systems Administrator 20 some odd years ago, I made my way towards backend development and eventually frontend development where, in comparison, is what I enjoy the most. I am self-taught and love learning new languages to help companies achieve their goals. Currently, I am teaching myself to code iOS apps in Xcode/Swift. To give my brain a break from coding, I enjoy photography, drawing and traveling.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            );
        case 'Resume':
            return(
                <div className="collapse" id={'collapseContent'+this.props.section.id} data-parent="#collapseContent">
                    <Experiences experiences={this.props.experiences} />
                </div>
            );
        case 'Let\'s Talk':
            return(
                <div className="collapse" id={'collapseContent'+this.props.section.id} data-parent="#collapseContent">
                    <div className="row">
                        <div className="container">
                            <div className="col-xs-12 col-sm-8">
                            <div id="contactTitle">Got a project? Let's create something awesome together!</div>
                            </div>
                        </div>
                    </div>

                    <div className="row">
                        <div className="container">
                            <div className="col-xs-12 col-sm-8 float-left">
                                <ContactForm />
                            </div>
                            <div className="d-none d-sm-block col-4 float-left">
                            <div id="bringTitle">Bring me in for:</div>
                            <ul id="bringList">
                                <li>Pitchwork or projects</li>
                                <li>Ideas or concept development</li>
                                <li>Experience design</li>
                                <li>Art direction</li>
                                <li>Creative strategy</li>
                                <li>UX</li>
                                <li>Design</li>
                                <li>Illustration</li>
                                <li>Storyboarding</li>
                            </ul>
                            </div>
                        </div>
                    </div>
                </div>
            );
        default:
            break;
      }
    return (
        <div></div>
    );
  }
}

export default SectionItem;